import React, { useLayoutEffect } from 'react'
import { TextPlugin } from "gsap/TextPlugin";
import gsap from 'gsap';


gsap.registerPlugin(TextPlugin);

const Card = () => {

    useLayoutEffect(() => {
    // ---------- selecting all
    const ctx = gsap.context(() => {
        // ---------- applying horizontal scroll animation
        const tl = gsap.timeline({repeat:-1, repeatDelay:2, yoyo:true});
        tl.to("#text", {duration: 4, text:"Here are the biggest enterprise technology acquisitions of 2021 so far, in reverse chronological order",});
        gsap.timeline({repeat:-1, yoyoyoyo:true}).from("#caret",{duration: 0.2}).to("#caret",{borderWidth:1, })
      });
  
      return () => ctx.revert();
    }, [])
    

  return (
    <div class="max-w-sm p-6 bg-white border border-gray-200 rounded-lg shadow dark:bg-gray-800 dark:border-gray-700">
            <a href="#">
              <h5 class="mb-2 text-2xl font-bold tracking-tight text-gray-900 dark:text-white">
                Noteworthy technology acquisitions 2021
              </h5>
            </a>
            <div>
            <span id="text" class="mb-3 font-normal text-gray-700 dark:text-gray-400">
        
            </span>
            <span id="caret" className='border-l-2 border-black'></span>
            </div>
            <a
              href="#"
              class="inline-flex items-center px-3 py-2 text-sm font-medium text-center text-white bg-blue-700 rounded-lg hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
            >
              Read more
              <svg
                class="rtl:rotate-180 w-3.5 h-3.5 ms-2"
                aria-hidden="true"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 14 10"
              >
                <path
                  stroke="currentColor"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M1 5h12m0 0L9 1m4 4L9 9"
                />
              </svg>
            </a>
          </div>
  )
}

export default Card
