import React from 'react'
import { Outlet } from 'react-router-dom'
import Navbar from './Navbar'

const Main = () => {
  return (
    <div> 
    <div>
        <Navbar />
    </div>
    <div className="bg-gray-100 min-h-[100vh] overflow-hidden">
      <Outlet/>
    </div>
    </div>
  )
}

export default Main
