import React, { useState } from "react";
import { useLayoutEffect, useRef } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Card from "./Card";
import axios from "axios";
import fileDownload from "js-file-download";

gsap.registerPlugin(ScrollTrigger);

const HorizontalScroll = () => {
  const comp = useRef(null);

  useLayoutEffect(() => {
    // ---------- selecting all
    const ctx = gsap.context(() => {
      const horizontalSections = gsap.utils.toArray(".horizontal-section");

      // ---------- applying horizontal scroll animation
      gsap.to(horizontalSections, {
        xPercent: -100 * (horizontalSections.length - 1),
        ease: "none",
        scrollTrigger: {
          trigger: "#container",
          pin: true,
          scrub: 1,
          //   snap: 1 / (horizontalSections.length - 1),
          end: () => "+=" + document.querySelector("#container").offsetWidth,
        },
      });
    });

    return () => ctx.revert();
  }, []);

  const [image, setImage] = useState("")
  
  const csvDownload = () => {
    axios.get("http://localhost:4000/get/img", {}).then(
      (response) => {
        let res = response.data
        let fr=new FileReader()
        // const blob = new Blob(res, { type: "'application/octet-stream'" });
        console.log(res);
        // setImage(res)
        downloadImage(res)
        let file = "data:image/png;base64,"+res 
        // fileDownload(file,"img.png");
      },
      (err) => {
        console.log(err);
      }
    );
  };

  const downloadImage = (res) => {
    const element = document.createElement('a');
    element.href = `data:image/png;base64,${res}`;
    element.download = 'image';
    element.click();
    element.remove()
  };
  // const csvDownload = () => {
  //   axios.get("http://localhost:4000/users/export", {}).then(
  //     (response) => {
  //       let res = response.data;
  //       console.log(res);
  //       fileDownload(res, 'employee.csv');
  //     },
  //     (err) => {
  //       console.log(err);
  //     }
  //   );
  // };

  return (
    <div id="wrapper ">
      <div
        id="container"
        className="flex w-[300%] overflow-x-hidden"
        ref={comp}
      >
        <div
          id="rt"
          className="h-screen w-screen flex justify-center items-center bg-black  horizontal-section backpos"
        >
          <div>
            {/* <img src={image && "data:image/png;base64,"+image} /> */}
            <Card />
            {/* <a
              className=" p-2 text-white bg-slate-500 rounded-md font-semibold text-center mt-2 hover:bg-slate-600 active:bg-slate-700"
              download
            >
              Download file
            </a> */}
            <button onClick={()=>{csvDownload()}} className=" p-2 text-white bg-slate-500 rounded-md font-semibold text-center mt-2 hover:bg-slate-600 active:bg-slate-700">Download Csv</button>
          </div>
        </div>
        <div
          className="h-screen w-screen flex justify-center items-center bg-gradient-to-r    from-black 
        to-white horizontal-section"
        >
          <Card />
        </div>
        <div
          className="h-screen w-screen flex justify-center items-center
         bg-white 
         horizontal-section"
        >
          <Card />
        </div>
      </div>
      <div className="h-screen bg-white"></div>
    </div>
  );
};

export default HorizontalScroll;
