import React from 'react'
import { useLayoutEffect ,useRef } from 'react'
import gsap from 'gsap'

const AnimatedCompo = () => {
    const comp = useRef(null)

    useLayoutEffect(() => {
     let ctx = gsap.context(()=>{
        const t1 = gsap.timeline()
        t1.from(["#title-1","#title-2","#title-3"],{
            opacity:0,
            y:"+=30",
            stagger:0.5
        }).to(["#title-1","#title-2","#title-3"],{
            opacity:0,
            y:"-=30",
            delay:0.3,
            stagger:0.5
        }).to('#intro-slider',{
            xPercent:"-100",
            duration:1.3,
            // delay:0.3
        }).from(["#welcom-1","#welcom-2"],{
            opacity:0,
            duration:0.5,
        })
     },comp)
     
     return ()=> ctx.revert()
    }, [])

  return (
    <div className=' relative h-100 top-28' ref={comp}>
        <div id='intro-slider' className='h-screen p-10 bg-gray-50 absolute top-0 left-0 font-spaceGrotesk z-10 w-full flex flex-col gap-10 tracking-tight'>
            <h1  id="title-1" className='text-5xl  md:text-9xl'>Software Engineer</h1>
            <h1 id="title-2" className='text-5xl  md:text-9xl'>Designer</h1>
            <h1 id="title-3" className='text-5xl  md:text-9xl'>FreeLancer</h1>
        </div>
    <div className='h-screen flex  flex-col bg-gray-950 justify-center place-items-center my-0 py-0'>
      <div id='welcom-1' className='text-5xl  md:text-9xl  font-bold text-gray-100 font-spaceGrotesk  '>Welcom!</div>
      <h1 id='welcom-2' className='text-5xl  md:text-9xl font-bold font- text-transparent font-spaceGrotesk transform scale-x-[1] scale-y-[-1] skew-x-[-45deg] ms-[-33px] md:ms-[-90px] bg-gradient-to-t from-gray-100 to-transparent bg-clip-text   mt-[-10px] md:mt-[-35px]'>Welcom!</h1>
    </div>
    </div>
  )
}

export default AnimatedCompo
